import React, { useState, useEffect, useCallback } from "react";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Skeleton from "react-loading-skeleton";
import axios from "axios";

const ProjectCard = ({ data, liveDemoUrl, projectLogo, projectPic }) => {
  const {
    name,
    description,
    svn_url,
    homepage,
    stargazers_count,
    languages_url,
    pushed_at,
  } = data;
  return (
    <Col md={6}>
      <Card className="card shadow-lg p-3 mb-5 bg-white rounded">
        {projectPic? (
          <Card.Img variant="top" src={projectPic} alt="Card image"/>
        ) : (
          <Card.Img variant="top" src="https://via.placeholder.com/2300x1000" alt="Card image"/>
        )
        }
        <Card.Body>
          <Card.Title as="h5">{name || <Skeleton />}</Card.Title>
          <Card.Text>{(!description)?"":description || <Skeleton count={3} />} </Card.Text>
          {svn_url ? <CardButtons svn_url={svn_url} homepage={homepage} liveDemoUrl={liveDemoUrl} /> : <Skeleton count={2} />}
          <hr />
          {languages_url ? (
            <Language languages_url={languages_url} repo_url={svn_url} />
          ) : (
            <Skeleton count={3} />
          )}
          {data ? (
            <CardFooter star_count={stargazers_count} repo_url={svn_url} pushed_at={pushed_at} liveDemoUrl={liveDemoUrl} />
          ) : (
            <Skeleton />
          )}
        </Card.Body>
      </Card>
    </Col>
  );
};

const CardButtons = ({ svn_url, homepage, liveDemoUrl }) => {
  return (
    <>
      <div class="container">
        <div class="row ">
          <div class="col-md-auto pl-0 pr-0 mb-3">
            <a href={svn_url} target=" _blank" className="btn btn-outline-secondary mr-3">
              <i className="fab fa-github" /> Project Repo
            </a>
          </div>
          <div class="col-md-auto pl-0 pr-0 mb-3">
            <a href={homepage} target=" _blank" className="btn btn-outline-secondary mr-3">
                <i className="fab fa-github" /> Project Website
            </a>
          </div>
          {liveDemoUrl &&
            <div class="col-md-auto pl-0 pr-0 mb-3">
                <a href={liveDemoUrl} target=" _blank" className="btn btn-primary mr-3">
                  Live Demo
                </a>
            </div>}
        </div>
        {/* <div class="row">
          {liveDemoUrl &&
            <div class="col-md-auto mb-3">
                <a href={liveDemoUrl} target=" _blank" className="btn btn-primary mr-3">
                  Live Demo
                </a>
            </div>}
        </div> */}
      </div>
    </>
  );
};

const Language = ({ languages_url, repo_url }) => {
  const [data, setData] = useState([]);

  const handleRequest = useCallback(async () => {
    try {
      const response = await axios.get(languages_url);
      return setData(response.data);
    } catch (error) {
      console.error(error.message);
    }
  }, [languages_url]);

  useEffect(() => {
    handleRequest();
  }, [handleRequest]);

  const array = [];
  let total_count = 0;
  for (let index in data) {
    array.push(index);
    total_count += data[index];
  }

  return (
    <div className="pb-3">
      Languages:{" "}
      {array.length
        ? array.map((language) => (
            <a
              key={language} 
              className="badge badge-light card-link"
              href={repo_url + `/search?l=${language}`}
              target=" _blank"
            >
              {language}:{" "}
              {Math.trunc((data[language] / total_count) * 1000) / 10} %
            </a>
          ))
        : "code yet to be deployed."}
    </div>
  );
};

const CardFooter = ({ star_count, repo_url, pushed_at, liveDemoUrl }) => {
  const [updated_at, setUpdated_at] = useState("0 mints");

  const handleUpdatetime = useCallback(() => {
    const date = new Date(pushed_at);
    const nowdate = new Date();
    const diff = nowdate.getTime() - date.getTime();
    const hours = Math.trunc(diff / 1000 / 60 / 60);

    if (hours < 24) {
      if (hours < 1) return setUpdated_at("just now");
      let measurement = hours === 1 ? "hour" : "hours";
      return setUpdated_at(`${hours.toString()} ${measurement} ago`);
    } else {
      const options = { day: "numeric", month: "long", year: "numeric" };
      const time = new Intl.DateTimeFormat("en-US", options).format(date);
      return setUpdated_at(`on ${time}`);
    }
  }, [pushed_at]);

  useEffect(() => {
    handleUpdatetime();
  }, [handleUpdatetime]);

  return (
    <div class="container">
        <div class="row ">
          <div class="col-md-8 pl-0 pr-0 mb-3 align-self-center">
            <p className="card-text">
              <a
                href={repo_url + "/stargazers"}
                target=" _blank"
                className="text-dark text-decoration-none"
              >
                <span className="text-dark card-link mr-4">
                  <i className="fab fa-github" /> Stars{" "}
                  <span className="badge badge-dark">{star_count}</span>
                </span>
              </a>
              <small className="text-muted">Updated {updated_at}</small>
            </p>
          </div>
          {/* {liveDemoUrl &&
                <div class="col-md-auto mb-3 pl-0 pr-0">
                    <a href={liveDemoUrl} target=" _blank" className="btn btn-primary mr-3">
                      Live Demo
                    </a>
                </div>} */}
        </div>
    </div>    
  );
};

export default ProjectCard;
