import React, { useState, useEffect, useCallback } from "react";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import Row from "react-bootstrap/Row";
import ProjectCard from "./ProjectCard";
import axios from "axios";

const dummyProject = {
  data : {
    name: null,
    description: null,
    svn_url: null,
    stargazers_count: null,
    languages_url: null,
    pushed_at: null,

  },
  liveDemoUrl: null
};
const API = "https://api.github.com";

const Project = ({ heading, username, length, specfic, fallBackData }) => {
  const allReposAPI = `${API}/users/${username}/repos?sort=updated&direction=desc`;
  const nReposAPI = `${API}/users/${username}/repos?sort=updated&direction=desc&per_page=${length}`;
  const specficReposAPI = `${API}/repos/${username}`;
  const dummyProjectsArr = new Array(length + specfic.length).fill(
    dummyProject
  );

  const [projectsArray, setProjectsArray] = useState([]);

  const fetchRepos = useCallback(async () => {
    let repoList = [];
    try {
      // // getting all repos
      // const response = await axios.get(allReposAPI);
      // // slicing to the length
      // repoList = [...response.data.slice(0, length)];

      // getting most recent n repos
      // const response = await axios.get(nReposAPI);
      // repoList = [...response.data.slice(0, length)];

      // adding specified repos
      var repoData = {
        data: null,
        liveDemoUrl: null,
      };
      try {
        for (let repo of specfic) {
          const response = await axios.get(`${specficReposAPI}/${repo.name}`);
          // repoData.data = response.data;
          // repoData.liveDemoUrl = repo.liveDemoUrl;
          repoList.push({
            data: response.data,
            liveDemoUrl: repo.liveDemoUrl,
            projectLogo: repo.projectLogo,
            projectPic: repo.projectPic,
          });

        }
      } catch (error) {
        console.error(error.message);
      }

      // adding n most recent repos


      // // TODO: remove the duplication.

      // // setting projectArray
      setProjectsArray(repoList);
    } catch (error) {
      console.error(error.message);
    }
  }, [nReposAPI, length, specfic, specficReposAPI]);

  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  return (
    <Jumbotron fluid id="projects" className="bg-light m-0">
      <Container className="">
        <h2 className="display-4 pb-5 text-center">{heading}</h2>
        <Row>
          {projectsArray.length
            ? projectsArray.map((project, index) => (
                <ProjectCard
                  key={`project-card-${index}`}
                  id={`project-card-${index}`}
                  data={project.data}
                  liveDemoUrl={project.liveDemoUrl}
                  projectLogo={project.projectLogo}
                  projectPic={project.projectPic}
                />
              ))
            : dummyProjectsArr.map((project, index) => (
                <ProjectCard
                  key={`dummy-${index}`}
                  id={`dummy-${index}`}
                  data={project.data}
                  liveDemoUrl={project.liveDemoUrl}
                />
              ))}
              {/* {projectsArray.map((project, index) => (
                <ProjectCard
                  key={`project-card-${index}`}
                  id={`project-card-${index}`}
                  value={project.data}
                />
              ))} */}
        </Row>
      </Container>
    </Jumbotron>
  );
};

export default Project;
